.movie {
    position: relative;
    width: 1000px;
    height: 300px;
    left: 50%;
    margin: 20px 0;
    margin-left: -500px;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 10px;
}

.movie .poster {
    position: absolute;
    width: 100%;
    max-width: 180px;
    left: 25px;
    top: 5%;
    border-radius: 5px;
}

.movie .title {
    position: absolute;
    width: max-content;
    margin-left: 220px;
    margin-top: 20px;
    font-size: 30px;
}

.movie .year {
    position: absolute;
    width: 40px;
    margin-left: 225px;
    margin-top: 70px;
    font-size: 13px;
}

.movie .genres {
    display: inline-flex;
    list-style-type: none;
    padding: 0;
    margin-left: 265px;
    margin-top: 70px;
    font-size: 13px;
}

.movie .genres .genre {
    margin: 0 5px;
}

.movie .summary {
    position: absolute;
    width: 700px;
    margin-left: 220px;
    font-size: 17px;
}

.movie .link {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background-color: transparent;
    cursor: pointer;
}